





























import { Notify } from 'vant';
import Vue from 'vue';
import {Component} from 'vue-property-decorator';

@Component({})
export default class Label extends Vue {
    get tags(){
      return this.$store.state.tagList;
    }
    rSelect:string[]=[];
    rSelect2:string[]=[];
    detail= [{
            title:'',
            children:[
                {type:'car',name:''},
                {type:'taxi',name:''},
                {type:'plane',name:''},
                {type:'train',name:''},
                {type:'subway',name:''}
            ]
        },{
            title:'',
            children:[
                {type:'foot',name:''},
                {type:'food1',name:""},
                {type:"food2",name:''},
                {type:'food3',name:''},
                {type:"food4",name:''}
            ]
        },{
            title:'',
            children:[
                {type:"play2",name:''},
                {type:'play',name:""},
                {type:'movie',name:''},
                {type:'run',name:''},
                {type:'traveling',name:""}
            ]
        },{
            title:'',
            children:[
                {type:"shopping",name:''},
                {type:'clothes',name:''},
                {type:'cosmetics',name:""},
                {type:'digital',name:''}
            ]
        },{
            title:'',
            children:[
                {type:'stay',name:''},
                {type:'daily',name:''},
                {type:'hcost',name:''},
                {type:'lottery',name:''},
                {type:'scost',name:''}
            ]
        },{
            title:"",
            children:[
                {type:"medical",name:''},
                {type:'drug',name:''},

            ]
        }
    ]
    ok(){       
        this.rSelect = [];
        this.$router.push({path:'/money'});
        this.$store.commit('createTag',this.rSelect2)
        this.rSelect2=[];
    }
    selected(type:any,item1:any,title:any){
        const tagTypes = this.tags.map((item: { type: any; })=> item.type)
        if(tagTypes.indexOf(type)>=0){
            Notify({ type: 'warning', message: '',
                duration:1000,});
            return
        }
        if(this.rSelect.indexOf(type)!==-1){
            this.rSelect.splice(this.rSelect.indexOf(type),1)//
            this.rSelect2.splice(this.rSelect2.indexOf(type),1)
        }else{
            if(this.rSelect.length<5){
                this.rSelect.push(type);
                let key = 'title';
                item1[key] = title;
                this.rSelect2.push(item1)
            }else{
                Notify({ type: 'warning', message: '5!',
                duration:1000,});
            }
        }
    }
    goBack(){
      this.$router.back();
    }
}
